import './css/header.css';
import { Link } from 'react-router-dom';

function Header() {
  return (
    <div>
    <div className="headerContainer">
      <h1 className="headerTitle">Will Westwood</h1>
      <div className="headerBTNHolder">
        <Link to={'/'} className="headerBTN">All Work</Link>
        <Link to={'/pers'} className="headerBTN">Personal Work</Link>
        <Link to={'/uni'} className="headerBTN">University Work</Link>
        <p className="headerBTN">About</p>
        <p className="headerBTN">Contact</p>
      </div>
    </div>
    </div>
  );
}

export default Header;
