import Header from './components/header.jsx';
import GItemCollection from './components/gallery.jsx';
import Footer from './components/footer.jsx';
import Detail from './components/detail.jsx';
import { useParams, Route } from 'react-router-dom';
import db from './db/db.js';

function App() {
  return (<div style={{height: "100vh"}}>
    <Header />

    <Route path={'/:tag?/:id'} component={GalleryDetail} />
    <Route path={'/:tag?/:id?'} component={Gallery} />

    <Footer />
  </div>)

}

function Gallery() {

  // eslint-disable-next-line
  let { tag, id } = useParams();
  // console.log(tag)

  if (!tag) {
    return (<GItemCollection tag={'all'}/>)
  } else {
    return (<GItemCollection tag={ tag }/>)
  }
}


function GalleryDetail() {
  
  // eslint-disable-next-line
  let { tag, id } = useParams()
  // console.log(id)
  
  if(id && Number(id)<db.galleryItems.length){
    return (<Detail detailID = {id} />);
  }else{
    return null;
  }
}

export default App;
